input:not(.magic-radio)[type=radio] {
  border: 1px solid #ccc !important;
  box-shadow: none !important;
}

input:not(.magic-radio)[type=radio]:checked::before {
  background-color: var(--color-primary) !important;
}

input:not(.magic-radio)[type=radio]:focus {
}

input:not(.magic-checkbox)[type=checkbox] {
  border: 1px solid var(--color-primary) !important;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .3) !important;
  transition: all .3s !important;

  width: 20px !important;
  min-width: 20px !important;
  height: 20px !important;
  //margin: 0 10px 0 0 !important;
}

input:not(.magic-checkbox)[type=checkbox]:checked {
  position: relative !important;
  &:before{
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    display: inline-block !important;
    width: 20px !important;
    height: 20px !important;
    content: "" !important;
  }

  &:after{
    position: absolute;
    content: "";
    top: 2px;
    left: 7px;
    box-sizing: border-box;
    width: 6px;
    height: 12px;
    transform: rotate(45deg);
    border-width: 2px;
    border-style: solid;
    border-color: #fff;
    border-top: 0;
    border-left: 0;
  }
  background: #6648fe;
}

input:not(.magic-checkbox)[type=checkbox]:focus {
  border-color: var(--color-primary) !important;
  box-shadow: 0 0 0 1px var(--color-primary) !important;
  outline: none !important;
}

.swal2-container.swal2-top-end > .swal2-popup, .swal2-container.swal2-top-right > .swal2-popup {
  margin-top: 35px !important;
}